import React from 'react';

import { useSelector } from 'react-redux';
import ImageTextPagenation from '../../Template/ImageTextPagenation';

export default function Home({ dataList }) {
  const popup = useSelector((state) => state.popup.value);
  return (
    <div className={`concept-detail-popup ${popup ? 'show-popup' : 'hide-popup'}`}>
      <ImageTextPagenation dataList={dataList} />
    </div>
  );
}
