/* eslint-disable no-nested-ternary */
import React, { useEffect, useRef, useState } from 'react';

import { Link, useParams } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import Button from '../Components/Button';
import { pageByState } from '../Store/pageState';
import { setPopupShow } from '../Store/popupState';

import ConceptDetailPopup from './Popup/ConceptDetailPopup';

import conceptData from '../Db/conceptData.json';
import assetsData from '../Db/assets.json';

export default function ConceptDetail() {
  const { id } = useParams();
  const dispatch = useDispatch();

  const keyNumber = conceptData.concept.findIndex((i) => i.id === id);

  const firstCategoryRef = useRef();
  const secondCategoryRef = useRef();
  const thirdCategoryRef = useRef();

  const firstCategoryData = conceptData.concept[keyNumber].categories.firstCategory;
  const secondCategoryData = conceptData.concept[keyNumber].categories.secondCategory;
  const thirdCategoryData = conceptData.concept[keyNumber].categories.thirdCategory;

  const [currentCategory, setCurrentCategory] = useState();
  const [currentData, setCurrentData] = useState(firstCategoryData);

  const setData = () => {
    if (currentCategory === 'first') {
      setCurrentData(firstCategoryData);
    } else if (currentCategory === 'second') {
      setCurrentData(secondCategoryData);
    } else if (currentCategory === 'third') {
      setCurrentData(thirdCategoryData);
    }
  };

  const popupReset = () => {
    dispatch(pageByState(0));
    dispatch(setPopupShow());
  };

  useEffect(() => {
    setData();
  });
  return (
    <div className="concept-detail">
      <div
        className="container"
      >
        <div
          className="category-area"
          style={{
            background: `${conceptData.concept[keyNumber].color}1F`,
          }}
        >
          <div
            className="category"
            style={{
              background: currentCategory === 'first'
                ? `linear-gradient(rgba(0, 0, 0, 0), ${conceptData.concept[keyNumber].color})` : 'none',
            }}
            onClick={() => {
              setCurrentCategory('first');
              popupReset();
            }}
            onMouseEnter={() => {
              setCurrentCategory('first');
            }}
            onMouseLeave={() => {
              setCurrentCategory();
            }}
            role="presentation"
            ref={firstCategoryRef}
          >
            <div className="category-content">
              <h2 className="sub-heading">
                {firstCategoryData.info.title}
              </h2>
              <p className="body-small">
                {firstCategoryData.info.description}
              </p>
            </div>
          </div>
          {
            secondCategoryData
              ? (
                <div
                  className="category"
                  style={{
                    background: currentCategory === 'second'
                      ? `linear-gradient(#FFFFFF0F, ${conceptData.concept[keyNumber].color})` : 'none',
                  }}
                  onClick={() => {
                    setCurrentCategory('second');
                    popupReset();
                  }}
                  onMouseEnter={() => {
                    setCurrentCategory('second');
                  }}
                  onMouseLeave={() => {
                    setCurrentCategory();
                  }}
                  role="presentation"
                  ref={secondCategoryRef}
                >
                  <div className="category-content">
                    <h2 className="sub-heading">
                      {secondCategoryData.info.title}
                    </h2>
                    <p className="body-small">
                      {secondCategoryData.info.description}
                    </p>
                  </div>
                </div>
              ) : null
          }
          {
            thirdCategoryData
              ? (
                <div
                  className="category"
                  style={{
                    background: currentCategory === 'third'
                      ? `linear-gradient(rgba(0, 0, 0, 0), ${conceptData.concept[keyNumber].color})` : 'none',
                  }}
                  onClick={() => {
                    setCurrentCategory('third');
                    popupReset();
                  }}
                  onMouseEnter={() => {
                    setCurrentCategory('third');
                  }}
                  onMouseLeave={() => {
                    setCurrentCategory();
                  }}
                  role="presentation"
                  ref={thirdCategoryRef}
                >
                  <div className="category-content">
                    <div className="sub-heading">
                      {thirdCategoryData.info.title}
                    </div>
                    <p className="body-small">
                      {thirdCategoryData.info.description}
                    </p>
                  </div>
                </div>
              ) : null
          }
          <div className="background-image">
            <img
              src={process.env.PUBLIC_URL + conceptData.concept[keyNumber].background}
              alt=""
            />
          </div>
        </div>
        <div
          className="title-area"
          style={{
            background: conceptData.concept[keyNumber].color,
          }}
        >
          <p className="body-large">
            0
            {keyNumber + 1}
          </p>
          <h1 className="heading-large">
            {conceptData.concept[keyNumber].title}
          </h1>
          <p className="body-medium">
            {conceptData.concept[keyNumber].desc}
          </p>

          <Link to="/concept">
            <Button
              buttonName="pre-button"
              icon={assetsData.icons[0].pre}
            />
          </Link>
        </div>
        <ConceptDetailPopup
          dataList={currentData}
        />
      </div>
    </div>
  );
}
