/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

export const popupState = createSlice({
  name: 'popup',
  initialState: {
    value: false,
  },
  reducers: {
    setPopupHide: (state) => {
      state.value = false;
    },
    setPopupShow: (state) => {
      state.value = true;
    },
  },
});

export const {
  setPopupHide,
  setPopupShow,
} = popupState.actions;

export default popupState.reducer;
