import { configureStore } from '@reduxjs/toolkit';
import zoomStateReducer from './zoomState';
import planeStateReducer from './planeState';
import fadeStateReducer from './fadeState';
import floorPlanReducer from './floorPlanState';
import homePopupReducer from './homePopupState';

import dataStateReducer from './dataState';
import pageStateReducer from './pageState';
import popupStateReducer from './popupState';
import zoneStateReducer from './zoneState';
import zoneDetailStateReducer from './zoneDetailState';

export default configureStore({
  reducer: {
    zoomState: zoomStateReducer,
    plane: planeStateReducer,
    fade: fadeStateReducer,
    floorPlan: floorPlanReducer,
    homePopupState: homePopupReducer,
    pager: pageStateReducer,
    popup: popupStateReducer,
    data: dataStateReducer,
    zone: zoneStateReducer,
    zoneDetail: zoneDetailStateReducer,
  },
});
