/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

export const pageState = createSlice({
  name: 'pager',
  initialState: {
    value: 0,
  },
  reducers: {
    setPageIncrease: (state) => {
      state.value += 1;
    },
    setPageDecrease: (state) => {
      state.value -= 1;
    },
    pageByState: (state, action) => {
      state.value = action.payload;
    },
  },
});

export const {
  setPageIncrease,
  setPageDecrease,
  pageByState,
} = pageState.actions;

export default pageState.reducer;
