import React from 'react';
import { useSelector } from 'react-redux';

import FloorInfo from '../Components/FloorInformation/FloorInfo';
import FloorMap from '../Components/FloorMap/FloorMap';
import ConceptDetailPopup from './Popup/ConceptDetailPopup';

export default function Floors() {
  const dataValue = useSelector((state) => state.data.value);

  return (
    <div className="floor">
      <FloorMap />
      <div className="floor-function-container">
        <FloorInfo />
      </div>
      <ConceptDetailPopup dataList={dataValue} />
    </div>
  );
}
