import React from 'react';

export default function Button({
  buttonName,
  clickEvent,
  icon,
}) {
  return (
    <div
      className={`button ${buttonName}`}
      onClick={() => {
        if (clickEvent) {
          clickEvent();
        } else {
          console.log('none function');
        }
      }}
      role="presentation"
    >
      {
        icon
          ? <img src={process.env.PUBLIC_URL + icon} alt={buttonName} />
          : buttonName
      }
    </div>
  );
}
