/* eslint-disable no-nested-ternary */
/* eslint-disable max-len */
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
  setPageIncrease,
  setPageDecrease,
  pageByState,
} from '../Store/pageState';

import assetsData from '../Db/assets.json';

export default function ImageTextPagenation({ dataList }) {
  const dispatch = useDispatch();
  const page = useSelector((state) => state.pager.value);
  const [alignState, setAlignState] = useState('');
  const [themeState, setThemeState] = useState('');

  const previousArrow = assetsData.icons[0].pre;
  const nextArrow = assetsData.icons[0].next;

  const decPage = () => {
    if (page > 0) {
      dispatch(setPageDecrease());
    } else if (page === 0) {
      pageByState(0);
    }
  };

  const incPage = () => {
    if (page < dataList.data.length - 1) {
      dispatch(setPageIncrease());
    } else if (page === dataList.data.length - 1) {
      pageByState(dataList.data.length - 1);
    }
  };

  useEffect(() => {
    if (dataList.data[page].centerAlign) {
      setAlignState('align-center');
    } else {
      setAlignState('');
    }

    if (dataList.data[page].themeLight) {
      setThemeState('theme-light');
    } else {
      setThemeState('');
    }
  }, [dataList, page]);

  return (
    <div className="image-text-pagenation">
      <div className="content-container">
        <div className={`content content-${alignState} content-${themeState}`}>
          {
            dataList.data[page].caption === undefined ? null
              : (
                <div className="caption-content">
                  <p className="body-small">
                    {dataList.data[page].caption}
                  </p>
                </div>
              )
          }
          {
            dataList.data[page].title === undefined ? null
              : (
                <div className="heading-content">
                  <h2 className="heading-large">
                    {dataList.data[page].title}
                  </h2>
                </div>
              )
          }
          {
           dataList.data[page].subTitle === undefined ? null
             : (
               <div className="sub-heading-content">
                 <h4 className="sub-heading">
                   {dataList.data[page].subTitle}
                 </h4>
               </div>
             )
          }
          {
            dataList.data[page].bodyText === undefined ? null
              : (
                <div className="body-content">
                  <p className="body-large">
                    {dataList.data[page].bodyText}
                  </p>
                </div>
              )
          }
        </div>

        {
          dataList.data[page].background === undefined ? null
            : (
              <div
                className={`background-image-content background-${dataList.data[page].rendering ? 'image-fitten' : ''}`}
              >
                <img
                  src={process.env.PUBLIC_URL + dataList.data[page].background}
                  className={
                      dataList.data[page].halfLayout ? 'image-half'
                        : dataList.data[page].rendering ? 'image-fitten' : ''
                    }
                  alt=""
                />
              </div>
            )
          }
      </div>

      <div className="pager">
        <div className="pager-container">
          <div
            className="arrow"
            onClick={() => { decPage(); }}
            role="presentation"
          >
            <img src={process.env.PUBLIC_URL + previousArrow} alt="pre" />
          </div>
          <div className="page-number">
            <p className="body-large">{page + 1}</p>
            <hr className="break-line" />
            <p className="body-large">{dataList.data.length}</p>
          </div>
          <div
            className="arrow"
            onClick={() => { incPage(); }}
            role="presentation"
          >
            <img src={process.env.PUBLIC_URL + nextArrow} alt="next" />
          </div>
        </div>
      </div>
    </div>
  );
}
