import React from 'react';
import { useSelector } from 'react-redux';

import zoneData from '../../../Db/zoneData.json';

export default function FloorElevator() {
  const currentZone = useSelector((state) => state.zone.value);
  const zoneDetail = useSelector((state) => state.zoneDetail.value);

  return (
    <div className={`floor-elevator-container ${zoneDetail ? 'show-floor-elevator-container' : ''}`}>
      <div className="zone-navigator-container">
        {
        zoneData.zone.map((data, i) => (
          <div
            className="floor-elevator"
            key={data.title}
          >
            <div
              className={`floor-icon ${i === currentZone ? 'highlight' : ''}`}
              role="presentation"
            />
            {/*
              <p className="number body-large">
              {data.number}
            </p>
        */}
          </div>
        ))
      }
      </div>
    </div>
  );
}
