import React, { useEffect, useState } from 'react';
import Goo from 'gooey-react';

import { Link } from 'react-router-dom';

import assetsData from '../Db/assets.json';
import conceptData from '../Db/conceptData.json';

export default function Concept() {
  const { innerWidth } = window;
  const [width, setWidth] = useState(innerWidth);

  const handleResize = () => {
    setWidth(window.innerWidth);
  };

  useEffect(() => {
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  });
  return (
    <div className="concept">
      <div className="concept-title">
        <div className="title-icon">
          <img src={process.env.PUBLIC_URL + assetsData.icons[0].brandLogo} alt="" />
        </div>
        <h2 className="heading-medium">
          The Bunkers
        </h2>
        <h3 className="heading-small">
          Platform for the world
        </h3>
      </div>
      <div
        className="container"
        style={{
          transform: `scale(${width / 1920})`,
        }}
      >
        {
            conceptData.concept.map((keyword, i) => (
              <div className="goo-container">
                <div className="goo-content">
                  <Link to={`/concept/${keyword.id}`}>
                    <h3 className="heading-small">
                      0
                      {i + 1}
                    </h3>
                    <h2 className="heading-large">
                      {keyword.title}
                    </h2>
                  </Link>
                </div>
                <Goo intensity="strong">
                  <svg className="goo">
                    <g>
                      <circle
                        style={{ animation: keyword.effect.first[0] }}
                        cx={`${keyword.effect.first[1]}%`}
                        cy={`${keyword.effect.first[2]}%`}
                        r={`${keyword.effect.first[3]}%`}
                        fill={keyword.effect.first[4]}
                      />
                      <circle
                        style={{ animation: keyword.effect.second[0] }}
                        cx={`${keyword.effect.second[1]}%`}
                        cy={`${keyword.effect.second[2]}%`}
                        r={`${keyword.effect.second[3]}%`}
                        fill={keyword.effect.second[4]}
                      />
                      <circle
                        style={{ animation: keyword.effect.third[0] }}
                        cx={`${keyword.effect.third[1]}%`}
                        cy={`${keyword.effect.third[2]}%`}
                        r={`${keyword.effect.third[3]}%`}
                        fill={keyword.effect.third[4]}
                      />
                    </g>
                  </svg>
                </Goo>
              </div>
            ))
          }
      </div>
    </div>
  );
}
