/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

export const zoneState = createSlice({
  name: 'zone',
  initialState: {
    value: 6,
  },
  reducers: {
    setZoneValueIncrease: (state) => {
      state.value += 1;
    },
    setZoneValueDecrease: (state) => {
      state.value -= 1;
    },
    zoneByState: (state, action) => {
      state.value = action.payload;
    },
  },
});

export const {
  setZoneValueIncrease,
  setZoneValueDecrease,
  zoneByState,
} = zoneState.actions;

export default zoneState.reducer;
