/* eslint-disable max-len */
import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { setZoomStateFalse } from '../../Store/zoomState';
import Button from '../Button';
import ConceptDetailPopup from '../../Pages/Popup/ConceptDetailPopup';

import zoneData from '../../Db/zoneData.json';

import assetsData from '../../Db/assets.json';
import { zoneByState } from '../../Store/zoneState';

export default function SectionInfo() {
  const dispatch = useDispatch();
  const zoomState = useSelector((state) => state.zoomState.value);
  const dataValue = useSelector((state) => state.data.value);

  const [visible, setVisible] = useState(0);
  const [showBackground, setShowBackground] = useState(false);
  const [overviewState, setOverviewState] = useState();

  useEffect(() => {
    if (zoomState) {
      setTimeout(() => {
        setVisible('fade-in');
      }, 2200);
    } else if (!zoomState) {
      setVisible('fade-out');
    }
  }, [zoomState]);

  return (
    <div className={`section-information ${visible}`}>
      <div
        className={`background-color ${showBackground ? 'show-background-color' : ''}`}
        style={{
          background: '#A3A3A3',
        }}
      />

      <Button
        buttonName="back-to-button"
        icon={assetsData.icons[0].pre}
        clickEvent={() => {
          dispatch(setZoomStateFalse());
          localStorage.setItem('zoomState', JSON.stringify(false));
        }}
      />

      <div className="floor-container">
        {
          zoneData.zone.map((data, i) => (
            <div
              className="zone"
              role="presentation"
              key={data.number}
              onClick={() => {
                localStorage.setItem('value', JSON.stringify(overviewState));
                dispatch(zoneByState(overviewState));
              }}
              onMouseEnter={() => {
                setOverviewState(i);
              }}
            >
              <Link to="/city/floors">
                <div
                  className="pin-title"
                  onMouseEnter={() => {
                    setShowBackground(true);
                  }}
                >
                  <p
                    className="body-medium"
                  >
                    {data.title}
                  </p>
                </div>
              </Link>
              <hr className="pin-line" />
            </div>
          ))
        }
      </div>

      <div className="section-map">
        <div className="zone-highlight-container">
          {
            zoneData.zone.map((data, i) => (
              <div
                className={
                  `zone-highlight
                  zone-highlight-${i}
                  ${overviewState === i
                    ? 'show-highlight' : ''}`
                  }
                style={{
                  background: zoneData.zone[i].color,
                }}
                key={data.number}
              />
            ))
          }
        </div>
        <img src={process.env.PUBLIC_URL + assetsData.image[0].sectionView} alt="section-map" />
      </div>

      <div className="zone-brief-container">
        {
            zoneData.zone.map((data, i) => (
              <div className={`zone-brief ${overviewState === i ? 'show-brief' : ''}`}>
                <div className="brief-heading-content">
                  <div className="icon">
                    <img src={process.env.PUBLIC_URL + data.icon} alt={data.number} />
                  </div>
                  <h2
                    className="heading-small"
                    style={{
                      color: zoneData.zone[i].color,
                    }}
                  >
                    {data.number}
                  </h2>
                  {/* <h3 className="heading-medium">
                  {data.title}
            </h3> */}
                </div>
                <div className="brief-body-content">
                  <p
                    className="body-large"
                    style={{
                      color: zoneData.zone[i].color,
                    }}
                  >
                    {data.desc}
                  </p>
                  <div className="zone-illustration">
                    <img src={process.env.PUBLIC_URL + data.illustration} alt="" />
                  </div>
                </div>
              </div>
            ))
          }
      </div>
      <ConceptDetailPopup dataList={dataValue} />
    </div>
  );
}
