/* eslint-disable no-nested-ternary */
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link, useLocation } from 'react-router-dom';

import { dataByState } from '../Store/dataState';
import { setPopupHide, setPopupShow } from '../Store/popupState';
import Button from './Button';

import assetsData from '../Db/assets.json';
import zoneData from '../Db/zoneData.json';

import buildingCoreConceptData from '../Db/buildingCoreConceptData.json';
import buildingOptionAdata from '../Db/buildingOptionAdata.json';
import buildingOptionBdata from '../Db/buildingOptionBdata.json';
import buildingOptionCdata from '../Db/buildingOptionCdata.json';
import buildingComparisonData from '../Db/buildingComparisonData.json';
import interiorConceptData from '../Db/interiorConceptData.json';
import renderingData from '../Db/renderingData.json';
import lightingConceptData from '../Db/lightingConceptData.json';
import FAndBConceptData from '../Db/FAndBConceptData.json';
import { setZoomStateFalse } from '../Store/zoomState';
import { pageByState } from '../Store/pageState';

export default function PopupNavigation() {
  const dispatch = useDispatch();
  const location = useLocation();

  const zoomState = useSelector((state) => state.zoomState.value);
  const popup = useSelector((state) => state.popup.value);
  const currentZone = useSelector((state) => state.zone.value);

  const [show, showState] = useState('flex');

  const mainButtonList = [
    {
      name: 'video',
      link: '/video',
      buttonName: 'video-link-button',
      icon: assetsData.icons[0].videoIcon,
    },
    {
      name: 'question',
      link: '/question',
      buttonName: 'question-link-button',
      icon: assetsData.icons[0].questionIcon,
    },
    {
      name: 'Session 1',
      link: '/concept',
      buttonName: 'session-01-link-button',
      icon: assetsData.icons[0].sessionIcon,
    },
    {
      name: 'Session 2',
      link: '/city',
      buttonName: 'session-02-link-button',
      icon: assetsData.icons[0].sessionIcon,
    },
  ];

  const buildingConceptButtonList = [
    {
      name: 'Architecture Concept',
      buttonName: 'building-concept-button',
      icon: assetsData.icons[0].architectureConceptIcon,
      data: buildingCoreConceptData,
    },
    {
      name: 'Bunkers 1',
      buttonName: 'option-a-button',
      icon: assetsData.icons[0].optionAIcon,
      data: buildingOptionAdata,
    },
    {
      name: 'Bunkers 2',
      buttonName: 'option-b-button',
      icon: assetsData.icons[0].optionBIcon,
      data: buildingOptionBdata,
    },
    {
      name: 'Bunkers 3',
      buttonName: 'option-c-button',
      icon: assetsData.icons[0].optionCIcon,
      data: buildingOptionCdata,
    },
    {
      name: 'Comparision Chart',
      buttonName: 'option-comparison-button',
      icon: assetsData.icons[0].chartIcon,
      data: buildingComparisonData,
    },
    {
      name: 'Interior Concept',
      buttonName: 'interior-concept-button',
      icon: assetsData.icons[0].interiorConceptIcon,
      data: interiorConceptData,
    },
    {
      name: 'Rendering Gallery',
      buttonName: 'rendering-gallery-button',
      icon: assetsData.icons[0].galleryIcon,
      data: renderingData,
    },
    {
      name: 'Lighting Concept',
      buttonName: 'lighting-concept-button',
      icon: assetsData.icons[0].lightingIcon,
      data: lightingConceptData,
    },
    {
      name: 'F&B Concept',
      buttonName: 'f-and-b-concept-button',
      icon: assetsData.icons[0].fAndBIcon,
      data: FAndBConceptData,
    },
  ];

  useEffect(() => {
    if (location.pathname === '/intro'
    ) {
      showState('none');
    } else {
      showState('flex');
    }
  }, [location, showState, popup]);
  return (
    <div
      className="navigation"
      style={{
        display: show,
      }}
    >
      <div className="button-container">
        <div className="fixed-button">
          {
            popup
              ? (
                <Button
                  buttonName="closePopupButton"
                  icon={assetsData.icons[0].closeIcon}
                  clickEvent={() => {
                    dispatch(setPopupHide());
                    dispatch(pageByState(0));
                    localStorage.setItem('zoomState', JSON.stringify(false));
                  }}
                />
              ) : (
                <Link to="/">
                  <Button
                    buttonName="home-link-button"
                    icon={assetsData.icons[0].brandLogoIcon}
                    clickEvent={() => {
                      dispatch(setZoomStateFalse());
                    }}
                  />
                </Link>
              )
          }
        </div>
        <div className="changed-navigation-area">
          <div
            className="main-navigation"
            style={{
              display: location.pathname === '/concept'
            || location.pathname === '/city'
            || location.pathname === '/city/floors'
            || location.pathname === '/question'
            || popup ? 'none' : 'flex',
            }}
          >
            {
            mainButtonList.map((data) => (
              <div className="button-box">
                <Link to={data.link}>
                  <Button
                    buttonName={data.buttonName}
                    icon={data.icon}
                  />
                </Link>
                <p className="body-medium">
                  {data.name}
                </p>
              </div>
            ))
          }
          </div>
          <div
            className="building-concept-navigation"
            style={{
              display: zoomState && location.pathname === '/city' ? 'flex' : 'none'
              || popup ? 'none' : 'flex',
            }}
          >
            {
            buildingConceptButtonList.map((data) => (
              <div className="button-box">
                <Link to={data.link}>
                  <Button
                    buttonName={data.buttonName}
                    icon={data.icon}
                    clickEvent={() => {
                      dispatch(setPopupShow());
                      dispatch(dataByState(data.data));
                    }}
                  />
                </Link>
                <p className="body-medium">
                  {data.name}
                </p>
              </div>
            ))
          }
          </div>

          {
            zoneData.zone.map((data, i) => (
              <div
                className={`zone-specification-navigation ${currentZone === i && location.pathname === '/city/floors' ? 'show-zone-specification-navigation' : ''}`}
              >
                {data.specification?.map((el) => (
                  <div className="button-box">
                    <Button
                      buttonName={el.info.title}
                      icon={el.info.icon}
                      clickEvent={() => {
                        dispatch(setPopupShow());
                        dispatch(dataByState(el));
                      }}
                    />
                    <p className="body-medium">
                      {el.info.title}
                    </p>
                  </div>
                ))}
              </div>
            ))
          }
        </div>
      </div>
    </div>
  );
}
