import React from 'react';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';

import { zoneByState, setZoneValueIncrease, setZoneValueDecrease } from '../../Store/zoneState';
import Button from '../Button';
import FloorElevator from './FloorController/FloorElevator';

import assetsData from '../../Db/assets.json';
import zoneData from '../../Db/zoneData.json';
import { setZoneDetailFalse, setZoneDetailTrue } from '../../Store/zoneDetailState';
// setZoneDetailTrue
export default function FloorInfo() {
  const dispatch = useDispatch();

  const currentZone = useSelector((state) => state.zone.value);
  const zoneDetail = useSelector((state) => state.zoneDetail.value);
  const totalZone = zoneData.zone.length;

  return (
    <div className="zone-information">
      <div className={`detail-view ${zoneDetail ? 'hide-detail-view' : ''}`}>
        <Button
          buttonName="back-to-vertical-button"
          icon={assetsData.icons[0].next}
          clickEvent={() => {
            dispatch(setZoneDetailTrue());
          }}
        />
      </div>

      <div className={`button-pre ${zoneDetail ? 'hide-button-pre' : ''}`}>
        <Link to="/city">
          <img src={process.env.PUBLIC_URL + assetsData.icons[0].pre} alt="pre" />
        </Link>
      </div>

      <div className="zone-information-container">
        <div
          className={`button button-up ${zoneDetail ? 'hide-button' : ''}`}
          role="presentation"
          onClick={() => {
            if (currentZone > 0) {
              dispatch(setZoneValueDecrease());
            } else if (currentZone === 0) {
              dispatch(zoneByState(0));
            }
          }}
        >
          <img src={process.env.PUBLIC_URL + assetsData.icons[0].up} alt="up" />
        </div>

        <div
          className={`button button-down ${zoneDetail ? 'hide-button' : ''}`}
          role="presentation"
          onClick={() => {
            if (currentZone < totalZone - 1) {
              dispatch(setZoneValueIncrease());
            } else if (currentZone === totalZone - 1) {
              dispatch(zoneByState(totalZone - 1));
            }
          }}
        >
          <img src={process.env.PUBLIC_URL + assetsData.icons[0].down} alt="down" />
        </div>

        <div className={`zone-heading-content ${zoneDetail ? 'reduce-zone-heading-content' : ''}`}>
          <h3
            className={`zone-number body-large ${zoneDetail ? 'hide-zone-number' : ''}`}
            style={{
              color: zoneData.zone[currentZone].color,
            }}
          >
            {zoneData.zone[currentZone].number}
          </h3>
          <div className="zone-title-rolling">
            <h3
              className={`zone-floors heading-small ${!zoneDetail ? 'hide-zone-floors' : ''}`}
              style={{
                color: zoneData.zone[currentZone].color,
              }}
            >
              {zoneData.zone[currentZone].number}
            </h3>
            <div
              className="zone-title-container"
              style={{
                top: zoneDetail ? '0px' : `calc(${currentZone * -8}vh)`,
                height: zoneDetail ? `${8}vh` : 'auto',
              }}
            >
              {
            zoneData.zone.map((data, i) => (
              <h2
                className={
                  `zone-title
                  ${currentZone === i ? 'heading-medium current-zone-title' : 'heading-small'}
                  ${zoneDetail && currentZone !== i ? 'hide-zone-title' : ''}
                  `
                }
                style={{
                  color: currentZone === i ? data.color : '#777777',
                }}
                onClick={() => {
                  if (!zoneDetail) {
                    dispatch(zoneByState(i));
                  }
                }}
                role="presentation"
              >
                {data.title}
              </h2>
            ))
          }
            </div>
          </div>
        </div>

        <div className={`zone-body-content ${zoneDetail ? 'show-zone-body-content' : ''}`}>
          <div className="icon-content">
            <img src={process.env.PUBLIC_URL + zoneData.zone[currentZone].icon} alt="current zone icon" />
          </div>
          <p className="zone-description body-small">
            {zoneData.zone[currentZone].longDesc}
          </p>

          <div className={`heading-button-content ${zoneDetail ? 'show-heading-button-content' : ''}`}>
            <Button
              buttonName="back-to-vertical-button"
              icon={assetsData.icons[0].closeIcon}
              clickEvent={() => {
                dispatch(setZoneDetailFalse());
              }}
            />
          </div>
        </div>
      </div>
      <FloorElevator />
    </div>
  );
}
