/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

export const zoneDetailState = createSlice({
  name: 'zoneDetail',
  initialState: {
    value: false,
  },
  reducers: {
    setZoneDetailFalse: (state) => {
      state.value = false;
    },
    setZoneDetailTrue: (state) => {
      state.value = true;
    },
  },
});

export const {
  setZoneDetailTrue,
  setZoneDetailFalse,
} = zoneDetailState.actions;

export default zoneDetailState.reducer;
